@charset "UTF-8";

/*
* FLOCSSベース 独自拡張
* https://github.com/hiloki/flocss
* core mixin/function/variable
* fundation normalize/reset/font/iconfont/
* layout header/footer/wrapper/main
* object component/project/utility
* page
* vendor
*/

// ---------------------------------------------------------------
// core - variable
// ---------------------------------------------------------------
@import "core/variables";

// ---------------------------------------------------------------
// core - function
// ---------------------------------------------------------------


// ---------------------------------------------------------------
// core - mixin
// ---------------------------------------------------------------
@import "core/mixins/_arrow.scss";
@import "core/mixins/_button-3d-color.scss";
@import "core/mixins/_button-border-color.scss";
@import "core/mixins/_button-flat-color.scss";
@import "core/mixins/_button-ghost-color.scss";
@import "core/mixins/_button-size.scss";
@import "core/mixins/_circle.scss";
@import "core/mixins/_clearfix.scss";
@import "core/mixins/_font-line.scss";
@import "core/mixins/_grid.scss";
@import "core/mixins/_line-clamp.scss";
@import "core/mixins/_link-color.scss";
@import "core/mixins/_media.scss";
@import "core/mixins/_middle-line.scss";
@import "core/mixins/_opacity.scss";
@import "core/mixins/_position.scss";
@import "core/mixins/_rounded.scss";

.page-favorite {
  .l-content {
    padding: 0 0 120px;
  }
  #favorite {
    padding: 21px 65px;
  }
}
.controlBox {
  display: flex;
  margin-bottom: 31px;
  .button {
    @include font-line(18, 54);
    border: 2px solid $color-secondary;
    display: inline-block;
    cursor: pointer;
    font-weight: 700;
    letter-spacing: 0.04em;
    text-decoration: none;
    text-align: center;
    width: 254px;
    height: 58px;
    position: relative;
  }
  .check_all {
    margin-right: 10px;
    @include font-line(14, 40);
    font-weight: 700;
    letter-spacing: 0.04em;
    cursor: pointer;
    .title {
      padding-left: 25px;
      &::before {
        height: 17px;
        width: 17px;
        top: -1px;
      }
    }
  }
  .go_inquiry_text {
    @include font-line(13, 40);
    font-weight: 700;
    margin: 0 10px 0 50px;
  }
  .go_inquiry {
    width: 260px;
    margin: 0;
  }
  .deleteButton {
    width: 193px;
    margin-left: 10px;
  }
}

dl.bukken_area {
  overflow: hidden;
  margin-bottom: 30px;
  border-top: 3px solid #69625e;
  .bukken_title {
    @include font-line(20, 24);
    color: #322e2c;
    font-weight: 700;
    letter-spacing: 0.2em;
    margin: 0;
    overflow: hidden;
    padding: 10px;
    a {
      text-decoration: none;
    }
    .shubetu {
      display: inline-block;
      vertical-align: text-top;
      letter-spacing: 0;
      .new {
        background-color: $color-sub;
        border: none;
        width: 90px;
        color: #fff;
      }
      .pricedown {
        background-color: $color-main;
        border: none;
        width: 90px;
        color: #fff;
      }
      @include clearfix();
    }
    .check {
      &__input {
      }
      &__parts {
        @include font-line(20, 24);
        padding-left: 30px;
        &:before {
          width: 21px;
          height: 21px;
          top: 5px;
        }
        &:after {
          @include font-line(25);
          top: 3px;
        }
      }
    }
  }
  .bukken_info {
    padding: 20px 0;
    margin: 0 10px;
    box-sizing: border-box;
    position: relative;
    font-size: 15px;
    line-height: 1.75;
    border-top: 2px dashed #69625e;
    a {
      border: none;
      padding: 0;
      color: $color-text01;
      text-decoration: none;
      display: block;
    }
    .pic {
      width: 285px;
      height: 250px;
      background: #fff;
      float: left;
      margin-bottom: 20px;
      margin-right: 20px;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        font-family: 'object-fit: contain;';
      }
      .stamp {
        width: 116px;
        height: 116px;
        border-radius: 50%;
        background-color: #000;
        color: #fff;
        @include font-line(16, 18);
        @include opacity(0.6);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        z-index: 2;
      }
    }
    .state {
      flex: 1 1 auto;
      margin-left: 10px;
      color: #322e2c;
      dl {
        display: flex;
        align-items: center;
        margin: 0 auto;
        min-height: 40px;
        border-bottom: 1px dashed #69625e;
        dt {
          @include font-line(16, 17);
          flex: 0 0 95px;
          padding: 0 20px;
        }
        dd {
          flex: auto;
          margin: 0;
          padding-right: 20px;
        }
      }
    }
  }
}
